<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-按钮尺寸1" class="vue-doc-element">按钮尺寸</h2>
<p class="vue-doc-element">按钮有六种类型：主按钮、次按钮、虚线按钮、危险按钮。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          
        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Select v-model="model1" style="width:200px" @on-change="handlerChanged" @on-select="handlerSelected">
        <Option value="" >空选项</Option>
        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model1"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:200px"</span> @<span class="hljs-attr">on-change</span>=<span class="hljs-string">"handlerChanged"</span> @<span class="hljs-attr">on-select</span>=<span class="hljs-string">"handlerSelected"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">""</span> &gt;</span>空选项<span class="hljs-tag">&lt;/<span class="hljs-name">Option</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in cityList"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.value"</span>&gt;</span></span><span class="hljs-template-variable"><span>{{</span> item.label }}</span><span class="xml"><span class="hljs-tag">&lt;/<span class="hljs-name">Option</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">Select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">cityList</span>: [
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-literal">true</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'New York1'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'London'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'London1'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Sydney'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Sydney1'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Ottawa'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Ottawa1'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Paris'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Paris1'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Canberra'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Canberra1'</span>
                    }
                ],
                <span class="hljs-attr">model1</span>: <span class="hljs-literal">true</span>
            }
        },
        <span class="hljs-attr">methods</span>: {
          handlerSelected() {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">"select"</span>,<span class="hljs-keyword">this</span>.model1)
          },
          handlerChanged() {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">"change"</span>,<span class="hljs-keyword">this</span>.model1)
          }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></span>
</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                cityList: [
                    {
                        value: true,
                        label: 'New York1'
                    },
                    {
                        value: 'London',
                        label: 'London1'
                    },
                    {
                        value: 'Sydney',
                        label: 'Sydney1'
                    },
                    {
                        value: 'Ottawa',
                        label: 'Ottawa1'
                    },
                    {
                        value: 'Paris',
                        label: 'Paris1'
                    },
                    {
                        value: 'Canberra',
                        label: 'Canberra1'
                    }
                ],
                model1: true
            }
        },
        methods: {
          handlerSelected() {
            console.log("select",this.model1)
          },
          handlerChanged() {
            console.log("change",this.model1)
          }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


