<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-icon形状1" class="vue-doc-element">icon&amp;形状</h2>
<p class="vue-doc-element">设置按钮形状和图标。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-iconshapes1" class="vue-doc-element">icon&amp;shapes</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">通过设置<code class="vue-doc-element">icon</code>属性在<code class="vue-doc-element">Button</code>内嵌入一个<code class="vue-doc-element">Icon</code>，或者直接在<code class="vue-doc-element">Button</code>内使用<code class="vue-doc-element">Icon</code>组件。</p>
<p class="vue-doc-element">使用<code class="vue-doc-element">Button</code>的<code class="vue-doc-element">icon</code>属性，图标位置将在最左边，如果需要自定义位置，需使用<code class="vue-doc-element">Icon</code>组件。</p>
<p class="vue-doc-element">通过设置<code class="vue-doc-element">shape</code>属性为<code class="vue-doc-element">circle</code>，可将按钮置为圆的形状。</p>

        </div>
        <div class="vue-doc-content-EN">
          <p class="vue-doc-element">Set the icon to insert an Icon to the Button, or use the Icon component inside the Button directly.</p>
<p class="vue-doc-element">The icon would appear at the leftmost side of the Button when
using the icon; use Icon component for different icons&#39; positions.</p>
<p class="vue-doc-element">Set the shape to circle to use a circle button.</p>

        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Select v-model="model10" multiple style="width:260px" @on-change="handlerChanged" @on-select="handlerSelected">
        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model10"</span> <span class="hljs-attr">multiple</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:260px"</span> @<span class="hljs-attr">on-change</span>=<span class="hljs-string">"handlerChanged"</span> @<span class="hljs-attr">on-select</span>=<span class="hljs-string">"handlerSelected"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in cityList"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.value"</span>&gt;</span></span><span class="hljs-template-variable"><span>{{</span> item.label }}</span><span class="xml"><span class="hljs-tag">&lt;/<span class="hljs-name">Option</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">Select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">cityList</span>: [
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'New York'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'New York'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'London'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'London'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Sydney'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Sydney'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Ottawa'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Ottawa'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Paris'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Paris'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'Canberra'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'Canberra'</span>
                    }
                ],
                <span class="hljs-attr">model10</span>: []
            }
        }
        ,
        <span class="hljs-attr">methods</span>: {
          handlerSelected() {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">"select"</span>,<span class="hljs-keyword">this</span>.model10)
          },
          handlerChanged() {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">"change"</span>,<span class="hljs-keyword">this</span>.model10)
          }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></span>
</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                cityList: [
                    {
                        value: 'New York',
                        label: 'New York'
                    },
                    {
                        value: 'London',
                        label: 'London'
                    },
                    {
                        value: 'Sydney',
                        label: 'Sydney'
                    },
                    {
                        value: 'Ottawa',
                        label: 'Ottawa'
                    },
                    {
                        value: 'Paris',
                        label: 'Paris'
                    },
                    {
                        value: 'Canberra',
                        label: 'Canberra'
                    }
                ],
                model10: []
            }
        }
        ,
        methods: {
          handlerSelected() {
            console.log("select",this.model10)
          },
          handlerChanged() {
            console.log("change",this.model10)
          }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


